import React from 'react';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
import Heading from "../components/Heading";
import LiveCard from "../components/LiveCard";
import NormalCard from "../components/NormalCard";
import ClipCardWithIcon from "../components/ClipCardWithIcon";

const params = {
  slidesPerView: 2.3,
  spaceBetween: 5,
  loop: true,

  breakpoints: {
    320: {
      slidesPerView: 1.8,
    },
    360: {
      slidesPerView: 2.1,
    },
    375: {
      slidesPerView: 2.3,
    }
  }
}

const CardSwiper = ({ contentSwiper }) => {
  return (
    <div>
      <Heading content={contentSwiper.headingData} />
      <div className="card-swiper">
        <Swiper {...params}>
          {contentSwiper.cards.map((item, i) => {
            return (
              <div>
                <div key={i} className="slider-content">
                  {item.isliveCard ? <LiveCard content={item.contentSwiperCard} /> : ''}
                  {item.isNormalCard ? <NormalCard content={item.contentSwiperCard} /> : ''}
                  {item.isClipCardWithIcon ? <ClipCardWithIcon content={item.contentSwiperCard} /> : ''}
                </div>
              </div>
            )
          })}
        </Swiper>
      </div>
    </div>
  );
};

export default CardSwiper;