import React, { useState, useEffect } from 'react'
import { Form, FormGroup, Input, Label } from 'reactstrap';

import Header from '../components/Header'
import PromoCard from '../components/SinglePromoCard'
import Back from '../assets/images/icon-back.svg'

const OrderPromoCode = () => {
  const people = [
    {
      name: "Michelle Valentine",
      discount: 40,
      code: 'mgrd123',
      validUntil: 'Feb 10, 2020',
      claimed: false,
    },
    {
      name: "Michelle Ziudith",
      discount: 40,
      code: 'mgrd123',
      validUntil: 'Feb 10, 2020',
      claimed: true,
    }
  ];

  const [searchTerm, setSearchTerm] = useState("");
  const [searchResults, setSearchResults] = useState([]);
  const handleChange = event => {
    setSearchTerm(event.target.value);
  };

  useEffect(() => {
    const results = people.filter(person =>
      person.name.toLowerCase().includes(searchTerm.toLowerCase())
    );

    setSearchResults(results);
  }, [searchTerm]);


  return (
    <>
      <Header content={{ title: 'Kode Promo', BtnLeftIcon: Back, BtnRightText: 'Selesai' }} />
      <div className="order order-promo">
        <div className="container">
          <div className="content-wrapper">
            <Form>
              <FormGroup>
                <Label className="c-white title-textbox" for="example">Promo</Label>
                <Input
                  type="search"
                  name="search"
                  id="exampleSearch"
                  className="textbox"
                  value={searchTerm}
                  onChange={handleChange}
                  placeholder="Musukkan kode promo"
                />
              </FormGroup>
            </Form>
            <div className="results-holder">
              {setSearchResults.length !== 0 &&
                searchResults.map(item => (
                  // <div className="card-holder">{item.name}</div>
                  <PromoCard content={item} />
                ))
              }
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default OrderPromoCode
