import React from 'react';
import { Link } from 'react-router-dom';
import { Form, FormGroup, Input, Button } from 'reactstrap';
import Heading from "../components/Heading";
import CustomButton from "../components/Button";
import CommentCard from "../components/CommentCard";

const FanComment = ({ content }) => {
  return (
    <div className="section-fan-comment">
      <div className="fan-question">
        <Heading content={{ heading: 'Pertanyaan Fans', headingColor: 'white' }} />
        <div className="card-question">
          <div className="profile-holder">
            <img src={content.userImage} className="profile-picture" alt={content.name} />
            <p className="profile-name">{content.name}</p>
          </div>
          <div className="text-holder">
            <p className="question-description">Pertanyaan untuk {content.artistName}</p>
            <p className="question-statement">{content.question}</p>
          </div>
        </div>
        {content.hasVoting ?
          <CustomButton content={content.buttonDetails} /> :
          <div className="spacing"></div>
        }

      </div>

      <div className="comments">
        <h2>Komentar</h2>
        <Form>
          <FormGroup>
            <Input
              type="text"
              name="comment"
              id="comment"
              className="input-box"
              placeholder="Tulis Komentar Kamu"
            />

            <Button color="link"><img src={require('../assets/images/icon-chat-gradient.svg')} alt="chat icon" /></Button>
          </FormGroup>
        </Form>

        <div className="comment-listing">
          {content.data.map((item, i) => {
            return (
              <CommentCard commentContent={item.commentCard} key={i} />
            )
          })}

          <CustomButton content={{ text: 'MORE', color: 'transparent-violet' }} />
        </div>
      </div>
    </div>
  );
};

export default FanComment;