import React, { useState } from 'react';
import { TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, CardTitle, CardText, Row, Col } from 'reactstrap';
import classnames from 'classnames';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
import LiveCard from "../components/LiveCard";
import NormalCard from "../components/NormalCard";
import ClipCardWithIcon from "../components/ClipCardWithIcon";
import CustomButton from "../components/Button";

const params = {
  slidesPerView: 5.3,
  spaceBetween: 10,
  breakpoints: {
    320: {
      slidesPerView: '4.5',
    },
    375: {
      slidesPerView: 5.3,
    }
  }
}

const EntertainmentTabs = ({ items }) => {
  const [activeTab, setActiveTab] = useState(1);

  const toggle = tab => {
    if (activeTab !== tab) setActiveTab(tab);
  }

  function ReturnTabs({ items }) {
    return (
      <div className="section-entertainment">
        <div className="tab-wrapper">
          <Nav tabs>
            <Swiper {...params}>
              {items.data.map((itemSwiper, i) => {
                return (
                  <NavItem className="tab-item" key={i}>
                    <NavLink
                      className={classnames("tab-link", { active: activeTab === itemSwiper.id })}
                      onClick={() => {
                        toggle(itemSwiper.id);
                      }}>
                      <div className="image-holder">
                        {/* <div className="icon"> */}
                        <img src={itemSwiper.icon} alt={itemSwiper.iconName} />
                        {/* </div> */}
                      </div>
                      <span className="tab-title">{itemSwiper.tabTitle}</span>
                    </NavLink>
                  </NavItem>
                )
              })}
            </Swiper>
          </Nav>

          <TabContent activeTab={activeTab}>
            {/* <div className="content-container"> */}
            {items.data.slice(0, activeTab).map((item, i) => {
              return (
                <TabPane tabId={item.id}>
                  <p className="tab-pane-title"> {item.tabTitle}</p>
                  <div className="card-listing">
                    {item.cards.map((item, j) => {
                      return (
                        <div className="card-holder" key={j}>
                          {item.isliveCard ? <LiveCard content={item.contentSwiperCard} /> : ''}
                          {item.isNormalCard ? <NormalCard content={item.contentSwiperCard} /> : ''}
                          {item.isClipCardWithIcon ? <ClipCardWithIcon content={item.contentSwiperCard} /> : ''}
                        </div>
                      )
                    })}
                  </div>
                  <CustomButton content={{ text: 'Lihat Semua Talent', color: 'transparent-violet' }} />
                </TabPane>
              )
            })}
            {/* </div> */}
          </TabContent >
        </div >
      </div>
    )
  }
  return (
    <div>
      <ReturnTabs items={items} />
    </div>
  );
}
export default EntertainmentTabs;