import React from 'react';
import Swiper from 'react-id-swiper';
import { Link } from 'react-router-dom';
import 'swiper/css/swiper.css';

import Icon from '../assets/images/img-idol-1.png'
import Nice from '../assets/images/icon-nice.svg'
import Chat from '../assets/images/icon-chat.svg'

const swiperData = [
  {
    image: Icon,
    name: "John Doe",
    message: "When you were young what was your...",
    votes: "12"
  },
  {
    image: Icon,
    name: "Jane Doe",
    message: "When you were young what was your...",
    votes: "12"
  },
  {
    image: Icon,
    name: "Jenny Doe",
    message: "When you were young what was your...",
    votes: "12"
  }
]

const params = {
  slidesPerView: 2.1,
  slidesPerGroup: 2.1,
  spaceBetween: 5
}

function SwiperQuestions({ content }) {
  return (
    <div className="swiper">
      <Swiper {...params}>

        {content.map((item, i) => {
          return (
            <div key={i} className="slider-fan-question">
              <div className="fan-data">
                <img src={item.image} className="fan-image" alt="fameo-user-icon" />
                <h5 className="fan-name text-white">{item.name}</h5>
              </div>
              <p className="fan-message text-white">
                {item.message}
                <Link to="/" className="read-more-text">Read more</Link>
              </p>
              <div className="message-action-list">
                <button className="btn-vote text-center">
                  <div className="vote-wrapper">
                    <img src={Nice} className="fan-nice" alt="fameo-nice" />
                    <span className="fan-votes text-center">{item.votes} vote</span>
                  </div>
                </button>
                <Link to="/">
                  <img src={Chat} className="fan-chat" alt="fameo-chat" />
                </Link>
              </div>
            </div>
          )
        })}
      </Swiper>
    </div>
  )
}

const FanQuestions = () => {
  return (
    <div className="fan-questions">
      <SwiperQuestions content={swiperData} />
    </div>
  );
};

export default FanQuestions