import React, { useState } from 'react';
import { TabContent, TabPane, Nav, NavItem, NavLink } from 'reactstrap';
import classnames from 'classnames';
import FanQuestions from '../components/QuestionsList'
import ProfileOrderList from '../containers/ProfileOrderList'
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
const params = {
  slidesPerView: 4.1,
  spaceBetween: 5,
}
const Tabs = ({ items }) => {
  const [activeTab, setActiveTab] = useState('1');
  const toggle = tab => {
    if (activeTab !== tab) setActiveTab(tab);
  }

  function ReturnTabs({ items }) {
    return (
      <div className="tab-wrapper">
        {/* <h5 className="text-white font-weight-bold">Order Saya</h5> */}
        <Nav tabs>
          <Swiper {...params}>
            {items.data.map((itemSwiper, i) => {
              return (
                <NavItem className="tab-item" key={i}>
                  <NavLink
                    className={classnames("tab-link", { active: activeTab === itemSwiper.id })}
                    onClick={() => {
                      toggle(itemSwiper.id);
                    }}>
                    <span>{itemSwiper.tabTitle}</span>
                  </NavLink>
                </NavItem>
              )
            })}
          </Swiper>
        </Nav>
        <TabContent activeTab={activeTab}>
          {items.data.map((item, i) => {
            return (
              <TabPane tabId={activeTab} key={i}>
                {item.fanQuestions === true && item.id === activeTab ?
                  <FanQuestions /> :
                  ''
                }
                {
                  item.orderList === true && item.id === activeTab ?
                    <ProfileOrderList /> : ''
                }
              </TabPane>
            )
          })}
        </TabContent>
      </div>
    )
  }

  return (
    <div>
      <ReturnTabs items={items} />
    </div>
  );
}
export default Tabs;