import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Form, FormGroup, Input, TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, CardTitle, CardText, Row, Col } from 'reactstrap'
import classnames from 'classnames'

// Components
import Header from '../components/Header'
import TaskBar from '../components/TaskBar'

import Toggler from '../assets/images/icon-toggler.svg'
import Sort from '../assets/images/icon-sort.svg'

const Search = () => {
  const people = [
    {
      name: "Michelle Ziudith",
      category: 'Film & TV'
    },
    {
      name: "Joe SNow",
      category: 'Film & TV'
    }
  ];

  const [searchTerm, setSearchTerm] = useState("");
  const [searchResults, setSearchResults] = useState([]);
  const handleChange = event => {
    setSearchTerm(event.target.value);
  };

  useEffect(() => {
    const results = people.filter(person =>
      person.name.toLowerCase().includes(searchTerm.toLowerCase())
    );

    setSearchResults(results);
  }, [searchTerm]);

  const [activeTab, setActiveTab] = useState('1');

  const toggle = tab => {
    if (activeTab !== tab) setActiveTab(tab);
  }

  return (
    <>
      <Header content={{ title: 'Cari Video', BtnLeftIcon: Toggler, BtnRightIcon: Sort }} />
      <div className="search">
        <div className="container">
          <Form className="search-bar">
            <FormGroup>
              <Input
                type="search"
                name="search"
                id="exampleSearch"
                value={searchTerm}
                onChange={handleChange}
                placeholder="Cari berdasarkan nama talent"
              />
              <span className="search-icon" />
            </FormGroup>
          </Form>
          <div className="filter-holder">
            <Nav tabs>
              <NavItem>
                <NavLink className={classnames({ active: activeTab === '1' })}
                  onClick={() => { toggle('1'); }}>Semua</NavLink>
              </NavItem>
              <NavItem>
                <NavLink className={classnames({ active: activeTab === '2' })}
                  onClick={() => { toggle('2'); }}>Ucapan</NavLink>
              </NavItem>
              <NavItem>
                <NavLink className={classnames({ active: activeTab === '3' })}
                  onClick={() => { toggle('3'); }}>Reaksi</NavLink>
              </NavItem>
              <NavItem>
                <NavLink className={classnames({ active: activeTab === '4' })}
                  onClick={() => { toggle('4'); }}>Tanya Jawab</NavLink>
              </NavItem>
              <NavItem>
                <NavLink className={classnames({ active: activeTab === '5' })}
                  onClick={() => { toggle('5'); }}>Tanya Jawab</NavLink>
              </NavItem>
            </Nav>
          </div>
          <TabContent className="results-holder" activeTab={activeTab}>
            <TabPane className="tab-content" tabId="1">
              {setSearchResults.length !== 0 &&
                searchResults.map(item => (
                  <div className="card-holder">{item.name}</div>
                ))
              }
            </TabPane>
            <TabPane className="tab-content" tabId="2">
              2
            </TabPane>
            <TabPane className="tab-content" tabId="3">
              3
            </TabPane>
            <TabPane className="tab-content" tabId="4">
              4
            </TabPane>
          </TabContent>
        </div>
        <TaskBar />
      </div>
    </>
  )
}

export default Search
