import React from 'react';

const Comment = ({ commentContent }) => {
  return (
    <div className="comment-card">
      <div className="image-wrapper">
        <img src={commentContent.userImage} className="profile-picture" alt={commentContent.name} />
      </div>

      <div className="text-wrapper">
        <p className="name">{commentContent.name}</p>
        <p className="date-time">{commentContent.date} at {commentContent.time}</p>
        <p className="comment">{commentContent.comment}</p>
      </div>
    </div>
  );
};

export default Comment;