import React from 'react'
import Timer from 'react-compound-timer'

const OrderRecord = (props) => {
  console.log('about', props.location.remainingTime.remaining);
  return (
    <div className="order-record-wrapper">
      <Timer
        initialTime={props.location.remainingTime.remaining}
        startImmediately={true}
        lastUnit="h"
        onStop={() => console.log(<Timer.Hours />)}
        direction="backward"
      >
        {({ start, resume, pause, stop, reset, getTimerState, getTime }) => (
          <React.Fragment>
            <Timer.Hours /> hours
            <Timer.Minutes /> minutes
            <Timer.Seconds /> seconds
            <div>Remaining time: {getTime()}</div>
          </React.Fragment>
        )}
      </Timer>
    </div>
  )
}

export default OrderRecord