import React, { useState, useEffect } from 'react'
import { useDropzone } from 'react-dropzone';
import { Form, FormGroup, Input, Label } from 'reactstrap'

import Header from '../components/Header'

import IdolImage from '../assets/images/img-idol-1.png'
import Back from '../assets/images/icon-left-arrow-two.svg'
import Camera from '../assets/images/icon-camera.svg'

const ProfileUpdate = () => {
  const [files, setFiles] = useState([]);
  const { getRootProps, getInputProps } = useDropzone({
    accept: 'image/*',
    onDrop: acceptedFiles => {
      setFiles(acceptedFiles.map(file => Object.assign(file, {
        preview: URL.createObjectURL(file)
      })));
    }
  });

  const thumbs = files.map(file => (
    <img
      src={file.preview}
      className="round-image"
      alt="fameo-user"
    />
  ));

  useEffect(() => () => {
    files.forEach(file => URL.revokeObjectURL(file.preview));
  }, [files]);

  return (
    <div className="profile-update-wrapper">
      <Header content={{ title: 'Ubah Profil', BtnLeftIcon: Back, BtnRightText: 'Sampin' }} />
      <div className="after-heading-wrapper">
        <div className="container">
          <div className="profile-image-wrapper">
            {files.length === 0 ? <img src={IdolImage} className="round-image" alt="fameo-profile" /> : thumbs}
            <div{...getRootProps({ className: 'dropzone' })}>
              <input{...getInputProps()} />
              <div className="dropzone-data">
                <img src={Camera} className="fameo-upload" alt="fameo-upload" />
              </div>
            </div>
          </div>
          <div className="update-form-wrapper">
            <Form>
              <FormGroup>
                <Label for="first-name" className="title-textbox">Nama Depan</Label>
                <Input type="text" name="first-name" id="first-name" className="textbox" placeholder="John" />
              </FormGroup>
              <FormGroup>
                <Label for="last-name" className="title-textbox">Nama Belakang</Label>
                <Input type="text" name="last-name" id="last-name" className="textbox" placeholder="Doe" />
              </FormGroup>
              <FormGroup>
                <Label for="username" className="title-textbox">Username</Label>
                <Input type="text" name="username" id="username" className="textbox" placeholder="johndoe" />
              </FormGroup>
              <FormGroup>
                <div className="password-holder">
                  <Label for="password" className="title-textbox">Password</Label>
                  <Input type="password" name="password" id="password" className="textbox" placeholder="Buat Kata Sandi" />
                </div>
              </FormGroup>
              <FormGroup>
                <Label for="exampleSelect" className="title-textbox">No. Handphone</Label>
                <div className="phone-holder">
                  <div className="select-holder">
                    <Input type="select" name="select" id="exampleSelect" className="textbox">
                      <option defaultValue>+62</option>
                    </Input>
                  </div>
                  <Input type="number" name="number" id="exampleNumber" className="textbox phone-input" placeholder="812 3456 7890" />
                </div>
              </FormGroup>
              <FormGroup>
                <Label for="email" className="title-textbox">Email</Label>
                <Input type="email" name="email" id="email" className="textbox" placeholder="johndoe@example.com" />
              </FormGroup>
            </Form>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ProfileUpdate