import React, { useState } from 'react'
import Header from '../components/Header'
import StarRatingComponent from 'react-star-rating-component';
import { Form, FormGroup, Label, Input } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faStar } from '@fortawesome/free-solid-svg-icons'

import Back from '../assets/images/icon-left-arrow-two.svg'
import Settings from '../assets/images/icon-settings.svg'

const ProfileOrderReview = () => {
  const [rating, setRating] = useState('1');

  function onStarClick(nextValue, prevValue, name) {
    setRating(nextValue)
  }

  return (
    <div className="order-review-wrapper">
      <Header content={{ title: 'Michelle Ziudith', BtnLeftIcon: Back, BtnRightIcon: Settings }} />
      <div className="after-heading-short-wrapper">
        <div className="container order-container">
          <div className="rating-wrapper">
            <h2 className="section-title text-center">Pilih Rating</h2>
            {/* <h2 className="hello">Rating from state: {rating}</h2> */}
            <div className="star-rating">
              <StarRatingComponent
                name="rate1"
                starCount={5}
                value={rating}
                starColor={'#FFB4CC'}
                emptyStarColor={'#fff'}
                onStarClick={onStarClick}
                renderStarIcon={(index, value) => {
                  return (
                    <FontAwesomeIcon icon={faStar} className="star-icon" />
                  );
                }}
              />
            </div>
          </div>
          <div className="review-wrapper">
            <Form className="form-review">
              <FormGroup>
                <Label for="exampleNumber" className="lbl-review">Review</Label>
                <Input type="text" name="number" className="input-review" id="exampleNumber" placeholder="Tulis review pesanan Kamu" />
              </FormGroup>
              <Input type="submit" value="KIRIM REVIEW" className="btn btn-submit" />
            </Form>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ProfileOrderReview